.card-related {
  margin-bottom: 80px;

  &__slider {
    .hero-slider__btn {
      color: #1078d3;
      opacity: 0.6;
      transform: translateY(-50%);

      @include mobile {
        transform: translateY(0);
      }
    }
  }

  @include desktop {
    margin-bottom: 50px;
  }

  @include mobile {
    margin-bottom: 30px;
  }
}
