/* stylelint-disable color-function-notation */
/* stylelint-disable alpha-value-notation */
/* stylelint-disable number-no-trailing-zeros */
.hero {
  padding-top: 15px;
  background: linear-gradient(117deg, #031367 5.76%, #1494f1 61.99%);

  &__title {
    margin: 0;
    margin-bottom: 14px;
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 48px;
    line-height: 130%; /* 62.4px */
    text-align: center;
    color: var(--txt-white, #fff);

    @include desktop {
      font-size: 28px;
    }
  }

  &__btn {
    // position: absolute;
    // left: 50%;
    // bottom: 30px;
    // z-index: 10;
    border: 2px solid var(--Blue, #1078d3);
    border-radius: 10px;
    padding: 20px 30px;
    font-family: "Furore";
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    line-height: 130%; /* 26px */
    color: var(--txt-h, #0c5eb8);
    background: rgba(255, 255, 255, 0.80);
  }

  &__modal {

  }
}

.hero-slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 1920px;

  &__images {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include desktop {
      overflow: hidden;
    }
  }

  &__img {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: -12px;
    padding-left: 20px;

    img {
      width: 375px;
    }

    @include desktop {
      position: absolute;
      left: 0;
      z-index: 10;
    }
  }

  &__text {
    margin-top: 20px;
    width: 100%;
    max-width: 451px;
    font-weight: 500;
    font-size: 22px;
    line-height: 130%; /* 28.6px */
    color: var(--txt-white, #fff);
  }

  &__img-left {
    @include desktop {
      transform: translateX(30%);
    }
  }

  &__info {
    position: absolute;
    left: calc((100% - 1170px) / 2) !important;
    bottom: 30px;
    z-index: 10;
    display: flex;
    width: 100%;

    @include desktop {
      left: 20px !important;
    }
  }

  &__pags {
    position: relative;
  }

  &__pag {
    counter-reset: pag;
    left: calc((100% + 768px) / 2) !important;
    bottom: 30px !important;
    display: flex;
    align-items: center;

    .swiper-pagination-bullet {
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid var(--txt-white, #fff);
      border-radius: 6px;
      padding: 10px 12px;
      width: 64px;
      height: 64px;
      background-color: transparent;
      opacity: 1;

      &::before {
        counter-increment: pag;
        content: "0"counter(pag);
        font-weight: 700;
        font-size: 24px;
        line-height: 102%; /* 24.48px */
        letter-spacing: 0.72px;
        color: var(--txt-white, #fff);
        transition: color 0.3s ease-in-out;
      }

      &::after {
        display: none;
      }
    }

    .swiper-pagination-bullet-active {
      border: 3px solid var(--txt-white, #fff);
      border-radius: 6px;
      font-weight: 700;
      font-size: 32px;
      line-height: 102%; /* 32.64px */
      letter-spacing: 0.96px;
      color: var(--txt-white, #fff);
      background-color: transparent;
    }

    @include desktop {
      display: none;
    }
  }

  &__btns {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    width: 100%;
    max-width: 1178px;
    transform: translate(-50%, -50%);
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 48px;
    height: 48px;
    color: #fff;
    opacity: 0.4;

    &::after {
      display: none;
    }

    svg {
      display: block;
      width: 24px;
      height: 24px;
      color: inherit;
      fill: currentcolor;
      -o-object-fit: cover;
      object-fit: cover;
    }

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:hover {
      opacity: 1;
    }

    &:active {
      opacity: 1;
    }

    &.swiper-button-disabled {
      opacity: 0.4;
      pointer-events: none;
    }
  }

  &__next {
    position: absolute;
    right: -10px;
    top: 50%;
    z-index: 10;
    transform: translateY(-100%);
  }

  &__prev {
    position: absolute;
    left: -10px;
    top: 50%;
    z-index: 10;
    transform: translateY(-100%);
  }
}
