.header {
  padding-top: 15px;
  padding-bottom: 15px;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
  }

  &__logo {
    display: flex;
  }

  &__logo-text {
    display: inline-block;
    margin-left: 5px;
    width: 88px;
    font-family: var(--second-family, sans-serif);
    font-size: 10px;
    line-height: 115%; /* 11.5px */
    letter-spacing: 0.4px;
    text-transform: lowercase;
    color: var(--txt-h, #0c5eb8);
  }

  &__tel {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 20px;
    line-height: 130%; /* 26px */
    letter-spacing: 0.6px;
    color: var(--txt-h, #0c5eb8);
  }

  &__bottom {
    display: flex;
    align-items: center;
  }

  &__catalog {
    margin-right: 40px;
  }

  &__bottom-search {
    border: 2px solid var(--Blue, #1078d3);
    border-radius: 10px;
    padding: 10px 12px;
    width: 100%;

    &::placeholder {
      font-family: var(--second-family, sans-serif);
      font-weight: 400;
      font-style: normal;
      font-size: 12px;
      line-height: 102%; /* 12.24px */
      letter-spacing: 0.36px;
      color: var(--txt-blue, #107bd7);
    }
  }
}

.nav {
  &__list {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    line-height: 130%; /* 15.6px */
    letter-spacing: 0.36px;
    color: var(--txt-h, #0c5eb8);
  }
}

.header-catalog {
  position: relative;
  display: flex;
  border-radius: 10px;
  padding: 12px 20px;
  background: var(--color-logo, linear-gradient(63deg, #031367 -37.36%, #1494f1 95.91%));
  cursor: pointer;

  &__text {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 18px;
    line-height: 102%; /* 18.36px */
    letter-spacing: 0.54px;
    color: var(--txt-white, #fff);
  }

  & svg {
    margin-right: 5px;
    width: 20px;
    height: 19px;
    stroke: #fff;
  }

  &__list {
    position: absolute;
    left: 0;
    top: 42px;
    display: none;
    border: 3px solid var(--Blue, #1078d3);
    border-radius: 10px;
    padding: 30px;
    width: max-content;
    background: var(--white, #fff);
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__link {
    font-family: var(--second-family, sans-serif);
    font-weight: 400;
    font-size: 18px;
    line-height: 102%; /* 18.36px */
    letter-spacing: 0.54px;
    color: var(--Blue, #1078d3);
  }

  &:hover .header-catalog__list {
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
  }
}
